<template>
  <div>
    <div class="m-container">
      <view-header title="排班管理"></view-header>
      <div class="m-table-div">
        <table>
          <thead>
            <th>日期</th>
            <th>专家名称</th>
            <th>上午</th>
            <th>下午</th>
            <th>上午余号</th>
            <th>下午余号</th>
            <th>是否停诊</th>
          </thead>
          <tbody>
            <tr v-for="item in 10" :key="item">
              <td>2017-12-25</td>
              <td>朱自清</td>
              <td>09:00 - 11: 30</td>
              <td>13:00 - 17: 00</td>
              <td>约满</td>
              <td class="green">余10(已约5)</td>
              <td>
                <el-switch
                  v-model="out"
                  active-color="#71D1CB"
                  inactive-color="#E2E2E2">
                </el-switch>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
      <div style="width: 100%; margin: 20px 0" class="flex flex-row flex-end">
        <el-pagination
          :page-sizes="[100, 200, 300, 400]"
          :page-size="100"
          layout="total, sizes, prev, pager, next, jumper"
          :total="400">
        </el-pagination>
      </div>
    </div>
  </div>
</template>
<script type="text/ecmascript-6">
import ViewHeader from 'base/view-header/view-header'
export default {
  components: {
    ViewHeader
  },
  data () {
    return {
      out: ''
    }
  }
}
</script>
<style lang="stylus" scoped rel="stylesheet/stylus">
@import "~common/stylus/base"
@import "~common/stylus/variable"
.m-container
  @extend .flex
  @extend .flex-column
  width: 100%
  height: 100%
  padding: 0 10px
.green
  color: $color-back-green_0
</style>


